<template>
  <div id="trash" class="detail">
    <div class="note-sidebar">
      <h3 class="notebook-title"></h3>
      <div class="menu">
        <div></div>
        <div></div>
      </div>
      <ul class="notes">
        <li v-for="note in trashNotes">
          <router-link :to="`trash?noteId=${note.id}`">
            <span class="date">{{ note.updatedAtFriendly }}</span>
            <span class="title">{{ note.title }}</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="note-detail">
      <div class="note-bar" v-if="currentTrashNote">
        <span> {{currentTrashNote.createdAtFriendly}}</span>
        <span> | </span>
        <span> {{currentTrashNote.updatedAtFriendly}}</span>
        <span> | </span>
        <span> {{belongTo}}</span>

        <a class="btn action" @click="onRevert"></a>
        <a class="btn action" @click="onDelete"></a>
      </div>
      <div class="note-title">
        <span>{{currentTrashNote.title}}</span>
      </div>
      <div class="editor">
        <div class="preview markdown-body" v-html="compiledMarkdown"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Auth from '@/api/auth'
import MarkdownIt from 'markdown-it'
let md = new MarkdownIt()

export default {
  name: "TrashDetail",
  data(){
    return{
      msg: '',
      currentTrashNote: {
        id: 3,
        title: '',
        content: '## hello',
        createdAtFriendly: '2',
        updatedAtFriendly: ''
      },
      belongTo: '',
      trashNotes: [
        {
          id: 3,
          title: '',
          content: '## hello',
          createdAtFriendly: '2',
          updatedAtFriendly: ''
        },
        {
          id: 3,
          title: '',
          content: '## hello',
          createdAtFriendly: '2',
          updatedAtFriendly: ''
        },
      ]
    }
  },
  created() {
    // 
    Auth.getInfo().then(res => {
      if(!res.isLogin){
        this.$router.push({ path: '/login'})
      }
    })
  },
  computed: {
    compliedMarkdown(){
      return md.render(this.currentTrashNote.content || '')
    }
  },
  methods: {
    onRevert(){

    },
    onDelete(notebook) {
      console.log('delete')
    }
  },
}
</script>

<style lang="less" scoped>
@import "../assets/css/note-sidebar.less";
@import "../assets/css/note-detail.less";
#trash {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;

  .note-bar {
    .action {
      float: right;
      margin-left: 10px;
      padding: 2px 4px;
      font-size: 12px;

    }
  }
}

</style>



